import React, { Component } from 'react'
import { Modal, Row, Col, Icon, Input, Button, Checkbox, Steps, Tag, Select, Tabs } from 'antd'
import { Player, BigPlayButton, ControlBar, VolumeMenuButton, LoadingSpinner } from 'video-react'
