const TabPane = Tabs.TabPane

export default class UploadPanel extends Component {
  state = {
    step: 1,
  }

  forwardStep () {
    this.setState((state) => {
      return { step: state.step + 1 }
    })
  }

  backwardStep () {
    this.setState((state) => {
      return { step: state.step - 1 }
    })
  }

  render () {
    let content = null
    switch (this.state.step) {
      case 1: {
        content = (
          <Col span={24}>
            <Step1 forwardStep={ this.forwardStep.bind(this) } />
          </Col>
        )
        break
      }
      case 2: {
        content = (
          <Col span={24}>
            <Step2
              forwardStep={ this.forwardStep.bind(this) }
              backwardStep={ this.backwardStep.bind(this) }
            />
          </Col>
        )
        break
      }
      case 3: {
        content = (
          <Col span={24}>
            <Step3
              forwardStep={ this.forwardStep.bind(this) }
              backwardStep={ this.backwardStep.bind(this) }
            />
          </Col>
        )
        break
      }
      case 4: {
        content = (
          <Col span={24}>
            <Step4
              backwardStep={ this.backwardStep.bind(this) }
            />
          </Col>
        )
        break
      }
      default: break
    }
    return (
      <Modal
        visible={ this.props.display }
        closable={ false }
        keyboard={ false }
        maskClosable={ false }
        footer={ null }
        width="1200px"
        bodyStyle={{
          background: '#2D2E2D',
          padding: 0,
        }}
      >
        <div
          onClick={ () => this.props.onCloseUpload('Close') }
          style={{
            position: 'absolute',
            top: 0,
            right: '5px',
            color: 'white',
            fontSize: '30px',
            zIndex: 99,
          }}
        >
          <Icon type="close" theme="outlined" />
        </div>
        <Row>
          <Col span={20} style={{
            padding: '10px',
          }}>
            <Steps>
              <Step
                status={ this.state.step >= 1 ? 'finish' : 'wait' }
                title={<StepTitle text="åŸºæœ¬ä¿¡æ¯" active={ this.state.step === 1 } />}
                icon={<Icon type="paper-clip" theme="outlined" />}
              />
              <Step
                status={ this.state.step >= 2 ? 'finish' : 'wait' }
                title={<StepTitle text="å›¢é˜Ÿä¿¡æ¯" active={ this.state.step === 2 } />}
                icon={<Icon type="team" theme="outlined" />}
              />
              <Step
                status={ this.state.step >= 3 ? 'finish' : 'wait' }
                title={<StepTitle text="è¡¥å……ä¿¡æ¯" active={ this.state.step === 3 } />}
                icon={<Icon type="pay-circle" theme="outlined" />}
              />
              <Step
                status={ this.state.step >= 4 ? 'finish' : 'wait' }
                title={<StepTitle text="é¢„è§ˆ" active={ this.state.step === 4 } />}
                icon={<Icon type="eye" theme="outlined" />}
              />
            </Steps>
          </Col>
        </Row>
        <Row>
          { content }
        </Row>
        <div style={{ clear: 'both' }}></div>
      </Modal>
    )
  }
}

